<template>
  <div>
    <div class="content">
      <div class="header">
        <div class="title">
          {{ game.title }}
        </div>
        Game Settings
      </div>
      <div class="header">
        Game Path
      </div>
      <div class="filePath">
        <div class="text">
          {{ path }}
        </div>
        <div
          class="button clickable"
          @click="setGameDir()"
        >
          Select Dir
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from 'vuex'
// eslint-disable-next-line no-unused-vars
import Game from 'store/models/game'
export default {
  data () {
    return {
      gamePath: null
    }
  },
  computed: {
    ...mapGetters({
      user: 'authentication/user',
      gameTag: 'ui/peekInfo'
    }),
    game () {
      return Game.find(this.gameTag)
    },
    path () {
      const trimLength = 38
      const path = this.gamePath
      if (path && path.length > trimLength) {
        const overgrowth = path.length - trimLength
        const clipStart = path ? Math.floor(overgrowth * 0.45) : 0
        return '...' + (path.substr(clipStart))
      } else return path
    }
  },
  mounted () {
    this.refreshGamePath()
  },
  methods: {
    setGameDir () {
      this.$store.dispatch('entities/games/selectDir', this.gameTag).then(path => {
        this.gamePath = path || this.gamePath
      })
    },
    refreshGamePath () {
      // eslint-disable-next-line no-undef
      this.gamePath = launcherGetGameFolder(this.gameTag)
    }
  }
}
</script>
<style lang="stylus" scoped>
.filePath {
  background-color: tint(black, 15%);
  padding-top: 13px;
  padding-left: 12px;
  height: 39px;
  position: relative;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;

  .text {
    max-width: calc(100% - 1000px);
  }

  .button {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    width: 100px;
    background-color: tint(black, 30%);
    text-align: center;
    padding-top: 13px;
  }
}

.header {
  font-family: politica, sans-serif;
  font-weight: 700;
  margin-bottom: 8px;

  &:nth-child(n+2) {
    margin-top: 16px;
  }
}

.content {
  padding: 16px;
}

.footer {
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 16px;
}
</style>
